.leaflet-container{
  position: fixed;
  width: 100%;
  height: 100%;
}

.app{
  position: fixed;
  width: 100%;
  height: 100%;
}

#root{
  position: fixed;
  width: 100%;
  height: 100%;
}

/* Leaflet crispness override */
.leaflet-container .leaflet-overlay-pane svg,
.leaflet-container .leaflet-marker-pane img,
.leaflet-container .leaflet-shadow-pane img,
.leaflet-container .leaflet-tile-pane img,
.leaflet-container img.leaflet-image-layer {
  max-width: none !important;
  /* Preserve crisp pixels with scaled up images */
  image-rendering: optimizeSpeed;             /* Legal fallback */
  image-rendering: -moz-crisp-edges;          /* Firefox        */
  image-rendering: -o-crisp-edges;            /* Opera          */
  image-rendering: -webkit-optimize-contrast; /* Safari         */
  image-rendering: optimize-contrast;         /* CSS3 Proposed  */
  image-rendering: crisp-edges;               /* CSS4 Proposed  */
  image-rendering: pixelated;                 /* CSS4 Proposed  */
  -ms-interpolation-mode: nearest-neighbor;   /* IE8+           */
}

.menu{
  display: block;
  background-color: #ece9e3;
  width: 100%;
  min-width: 100%;
  height: 100%;
  border-radius: 8px;
  height: 80vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.desktopControl{
  width: 12vw;
  display: block;
  min-width: 220px;
  padding-top: 10vh;
  padding-bottom: 10vh;
  padding-right: 20px;
  height: 80%;
  margin-top: 0px !important;
}

h1{
  text-align: center;
  
}

.productHeader{
  line-height: 0.6;
  padding: 8px 2px 0px 2px;
}

.scale{
  max-width: 100%;
}

.scaleWrapper{
  padding-left: 20px;
  padding-right: 30px;
}

.metadata{
  line-height: 0.4;
  display: flex;
  width: 100%;
}

.left-metadata{
  float: left;
  width: 50%;
  height: 100%;
}

.right-metadata{
  float: left;
  width: 50%;
  height: 100%;
}

.left-metadata-entry{
  padding-left: 20px;
}

.right-metadata-entry{
  padding-left: 20px;
}

p {
  font-size: 14px;
}

.settings{
  width: 100%;
  margin-top: 10px;
}
.opacitySlider{
  width: 80% !important;
  margin: auto !important;
  display: block !important;
  padding-bottom: 10px !important;
  background-color: transparent !important;
}

.settings p{
  margin-block-start: 0px;
  padding-left: 20px;
}

.switch{
  position: relative;
  display: inline-block;
  width: 48px;
  height: 22px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 24px;
  width: 24px;
  left: 0px;
  bottom: -1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #4ed164;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4ed164;
}

input:checked + .slider:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  transform: translateX(24px);
}

.slider.round {
  border-radius: 22px;
}

.slider.round:before {
  border-radius: 50%;
}

.toggleWrapper{
  display: block;
  width: 100%;
  margin-left: 20px;
  margin-top: 12px;
}

.ipolSwitch{
  float: left;
  width: 20%;
}

.ipolLegend{
  float: left;
  width: 80%;
}

.animationSlider{
  width: 90%;
}

.animationControl{
  margin-left: 0px !important;
  padding-left: 5vw;
  width: 100vw;
  height: 100%;
  display: block;
}

.animationSlider{
  background-color: transparent !important;
}

.animationToolkit{
  display: block;
}

#animationButton{
  display: block;
  margin: auto;
  position: relative;
  padding: 2%;
}

.mobileMenu, .mobileControl, .productHeaderMobile{
  display: none;
}

@media screen and (max-width: 800px){
  .productHeader{
    display: none;
  }

  .productHeaderMobile{
    display: block;
    line-height: 1.0;
    padding: 4px 2px 0px 2px;
  }

  .desktopControl{
    width: 100vw;
    padding: 0px;
    display: block;
    position: relative;
    margin: 0px !important;
  }
  
  .menu{
    width: 100vw !important;
    height: 100px;
    display: block;
    background-color: #ece9e3;
    border-radius: 0px !important;
  }

  div.menu div.productHeaderMobile h1 {
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .scaleWrapper, .metadata, .settings{
    display: none;
  }

  #animationButton{
    width: 28px;
    height: 28px;
    background-color: rgb(71, 255, 102);
  }
}
