input[type=range].opacitySlider {
    -webkit-appearance: none;
    margin: 10px 0;
  }
  input[type=range].opacitySlider:focus {
    outline: none;
  }
  input[type=range].opacitySlider::-webkit-slider-runnable-track {
    height: 4px;
    cursor: pointer;
    box-shadow: 0px 0px 1px #000000, 0px 0px 0px #0d0d0d;
    background: #f0f0f0;
    border-radius: 15.2px;
    border: 0px solid #010101;
  }
  input[type=range].opacitySlider::-webkit-slider-thumb {
    box-shadow: 0px 0px 1px #000031, 0px 0px 0px #00004b;
    border: 0px solid #56001e;
    height: 24px;
    width: 24px;
    border-radius: 40px;
    background: #4ed164;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -10px;
  }
  input[type=range].opacitySlider:focus::-webkit-slider-runnable-track {
    background: #f0f0f0;
  }
